<template>
	<view>
		<cu-custom bgColor="bg-gradual-blue" :isBack="true">
			<block slot="content">我的预约</block>
		</cu-custom>
		<!-- <homecom class="homclass"></homecom> -->
		<view class="mainsub">
			<view class="text-xsl padding" v-if="listdata.length == 0">
				<text class=" cuIcon-info text-blue"></text>
				<view class="textclass">没有可查看的数据</view>
			</view>
			<view class="card-main" v-for="(item,i) in listdata" :key="i" style="margin-bottom: 20px;">
				<view class="card-top">
					<view class="card-toptitle01">
						<view class="card-content">
							<view class="card-maintitle01">
								预约账号：
							</view>
							<view class="card-maintitle02">
								{{item.account}}
							</view>
						</view>
					</view>
				</view>
				<view class="card-content">
					<view class="card-maintitle01">
						预约人：
					</view>
					<view class="card-maintitle02">
						{{item.people}}
					</view>
				</view>
				<view class="card-top">
					<view class="card-toptitle01">
						<view class="card-content">
							<view class="card-maintitle01">
								预约座位：
							</view>
							<view class="card-maintitle02">
								{{item.seatdesc}}
							</view>
						</view>
					</view>
				</view>
				<view class="card-content">
					<view class="card-maintitle01">
						申请时间：
					</view>
					<view class="card-maintitle02">
						{{item.datastr}}
					</view>
				</view>
				<button class="cu-btn smallbut" @click="see(item.id,item.people,item.seatid,item.datastr,item.account)"><text style="margin-right: 5px;">详情</text><text
						class="cuIcon-attentionfill"></text></button>
			</view>
		</view>
	</view>
</template>

<script>
	import request from '@/api/api.js'
	import urls from '@/api/request.js'
	import homecom from "@/components/homecom/homecom.vue"

	export default {
		onLoad() {


		},
		mounted() {
			this.init(0)
		},
		components: {
			homecom
		},
		data() {
			return {
				listdata: [],
				scrollLeft: 0,
				flagshow: true,
			}
		},
		methods: {
			async init() {
				let userinfo = JSON.parse(uni.getStorageSync('userinfo'))
				let queryobj = {
					account: userinfo.account
				}
				let resdata = await new this.Request(this.Urls.m().byaccountquery, queryobj).modepost()
				this.listdata = resdata
			},
			pass(id) {
				uni.showModal({
					content: '确定要审批通过吗？',
					cancelText: '取消',
					confirmText: '确定',
					success: res => {
						if (res.confirm) {
							this.setstatus(1, id)
						}
					}
				})
			},
			unpass(id) {
				uni.showModal({
					content: '确定要拒绝审批吗？',
					cancelText: '取消',
					confirmText: '确定',
					success: res => {
						if (res.confirm) {
							this.setstatus(2, id)
						}
					}
				})
			},
			see(id,people,seatid,datastr,account) {
				uni.navigateTo({
					url: `../appdetail/appdetail?id=${id}&people=${people}&seatid=${seatid}&datastr=${datastr}&account=${account}`
				});
			},

		}
	}
</script>

<style scoped>
	.mainsub {
		padding-left: 5%;
		padding-right: 5%;
	}

	.card-main {
		width: 100%;
		height: 150px;
		background-color: #f1f1f1;
		border-radius: 5px;
		margin-top: 10px;
		padding: 5%;
		position: relative;
		border: 1px #f1f1f1 solid;
		box-shadow: darkgrey 2px 2px 2px 2px;
	}

	.card-top {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.card-content {
		display: flex;
		justify-content: flex-start;
		align-items: center;
	}

	.smallbut {
		position: absolute;
		right: 0;
		bottom: 0;
		background-color: #0081ff;
		color: #fff;
	}

	.card-maintitle01 {
		font-weight: bold;
	}

	.card-maintitle01,
	.card-maintitle02 {
		margin-top: 10px;
	}

	.card-toptitle02 {
		margin-top: 10px;
	}

	.text-xsl {
		margin-top: 20%;
		text-align: center;
		font-size: 120px;
	}

	.textclass {
		margin-top: 5px;
		font-weight: 600;
		font-size: 15px;
		color: darkgray;
	}
</style>
